
* {
    box-sizing: border-box;
}


html {
    font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
    color: #333333;
}

.articles {
    display: flex;
    flex-flow: row wrap;
    max-width: 800px;
    margin: 0 auto;

    .article {
        width: calc((100% - 2em)/3);
        margin-left: 1em;
        border: 1px solid silver;
        padding: 1em;
        display: flex;
        flex-direction: column;
        justify-content: space-between;

        &:first-child {
            margin-left: 0;
        }

        .image {
            img {
                width: 100%;
            }
        }

        a {
            text-decoration: none;
            display: flex;
            flex-direction: column;

            h2 {
                font-size: 1.125em;
                color: #999999;
            }

            .author {
                margin-bottom: 1em;
            }

            .published {
                margin-top: auto;
                font-size: 0.875em;
                color: #999999;
            }
        }
    }
}

